'use strict';

(function () {

angular.module("nf.home")
    .config(config);

// dependecy injection
config.$inject = ["$stateProvider", "$urlRouterProvider"];

function config($stateProvider, $urlRouterProvider) {

    var state = {
        name: 'home',
        url: '/home',
        templateUrl: 'views/home/home.html',
        controller: 'homeController',
        controllerAs: "c",
        resolve: {
            productsList: productsList
        }
    };

    $stateProvider.state(state);


    productsList.$inject = ['$q', 'furgoneSrv', 'carsSrv'];

    function productsList($q, furgoneSrv, carsSrv) {
        var response = {};

        var furgoneListCall = furgoneSrv.getFurgone().then(function (data) {
            return data.data.furgone;;
        }).catch(function (response) {
            return response;
        });

        var carsListCall = carsSrv.getCars().then(function (data) {
           return data.data.cars;
        }).catch(function (response) {
            return response;
        });

        //TODO spinner on
        return $q.all([furgoneListCall, carsListCall]).then(function (data) {
            //TODO spinner off
            var failedServices = 0, errors = [];
            angular.forEach(data, function (value, key) {
                //TODO manage errors
                if (typeof value.status !== 'undefined' && value.status != 200) {
                    errors.push(value);
                    failedServices += 1;
                }
            });
            if (failedServices > 0) {
                //TODO show errors
                return $q.reject();
            } else {
                response.furgoneList = data[0];
                response.carsList = data[1];
                return response;
            }
        });

    }
}
})();
