'use strict';

(function () {

    angular
        .module('nf.dataservices')
        .factory('promotionsSrv', promotionsSrv);

    promotionsSrv.$inject = [
        '$log',
        'dataServiceUtil'
    ];

    function promotionsSrv($log, dataServiceUtil) {

        return {
            getPromotions: getPromotions
        };

        function getPromotions(param) {
            try {
                return dataServiceUtil.callNFService("getPromotions", param).then(handleSuccess);
            } catch (e) {
                $log.error("*** si e' verificato un errore nel servizio get getPromotions ***");
            }

        }

        function handleSuccess(response) {
            return response;
        }

    }

})();
