(function (angular) {

    angular.module('directives.product').directive("nfProduct", productFn);

    function productFn() {
        var directive = {
            restrict: 'E',
            templateUrl: 'directives/product/product.template.html',
            scope: {
                data: '=',
                openBookProductForm: '&',
                openProductDetails: '&'
            },
            controller: ctrlFn,
            controllerAs: 'c',
            bindToController: true
        };

        return directive;

        function ctrlFn() {

            var self = this;

            self.$onInit = init;

            function init() {
                
            }

        }

    }

})(window.angular);