'use strict';

/**
 *--Utilizzo:
 * -Per lanciare l'alert è neccessario:
 *  1) importare in app.js la dipendenza "blocks.alert";
 *  2) importare nel controllore utilizzato il servizio "alertSrv";
 *  3) utilizzare la funzione alertSrv.showAlert() per la visualizzazione.
 *--Configurazione:
 *  Il componente prevede la seguente configurazione:
 *      default) alertSrv.showAlert("stringa messaggio")
 *          In questo caso viene visualizzato un alert di tipo info (celeste)
 *      custom) alertSrv.showAlert(conf) dove conf è un oggetto.
 *          var conf = {};
 *          conf.type = "success,info,warning,danger"
 *                      "success" = verde
 *                      "info" = celeste
 *                      "warning" = arancione
 *                      "danger" = rosso
 *
 *          conf.msg = "testo alert";
 *          conf.position = “top bottom left right”
 *          conf.size = “lg,sm”
 *
 *-- OSS:	I parametri con la "," sono esclusivi mentre quelli senza sono combinabili
 *          EX: conf.position = "top left";
 **/

(function () {

    angular
        .module('directives.alert')
        .factory('alertSrv', alertSrv);

    alertSrv.$inject = [
        '$log',
        '$compile',
        '$document',
        '$rootScope',
        '$timeout'
    ];

    function alertSrv($log, $compile, $document, $rootScope, $timeout) {

        return {
            showAlert : showAlert,
            close: close
        };

        function showAlert(data){
            if($document.find('bl-alert')){
                $document.find('bl-alert').remove();
            }

            var conf = {};
            var durataAlert = 5000;
            
            if(!angular.isObject(data)){
                conf.type = "info";
                conf.msg = data;
                conf.size = "large";
                conf.position = "top";
            }
            else{
                conf = data;
            }
            var template = "<bl-alert config='"+btoa(JSON.stringify(conf))+"'></bl-alert>";
            var content = $compile(template)($rootScope);
            $document.find('body').append(content);

           
            if(conf.type === "danger"){
                durataAlert = 10000;
            }
            
        }

    }

})();
