(function (angular) {

    angular.module('directives.productSearch').directive("nfProductSearch", productSearchFn);

    function productSearchFn() {
        var directive = {
            restrict: 'E',
            templateUrl: 'directives/productSearch/productSearch.template.html',
            scope: {
                cancel: '&'
            },
            controller: ctrlFn,
            controllerAs: 'c'
        };

        return directive;

        function ctrlFn() {

            var self = this;

            self.bookingDateConfig = [{
                    name: 'start',
                    label: 'Ritiro',
                    model: new Date(),
                    format: 'dd/MM/yyyy',
                    options: {
                        dateDisabled: function (data) {
                            var date = data.date,
                                mode = data.mode;
                            return mode === 'day' && false;
                        },
                        formatYear: 'yy',
                        maxDate: new Date(2020, 5, 22),
                        minDate: new Date(),
                        startingDay: 1
                    },
                    state: {
                        opened: false
                    },
                    toggle: function () {
                        this.state.opened = !this.state.opened;
                    },
                    selectionDisabled: function () {
                        return false;
                    }
                },
                {
                    name: 'end',
                    label: 'Consegna',
                    model: '',
                    format: 'dd/MM/yyyy',
                    options: {
                        dateDisabled: function (data) {
                            var date = data.date,
                                mode = data.mode;
                            var validEndDate = bookingValidEndDate();
                            return mode === 'day' && !validEndDate;
                        },
                        formatYear: 'yy',
                        maxDate: new Date(2020, 5, 22),
                        minDate: new Date(),
                        startingDay: 1
                    },
                    state: {
                        opened: false
                    },
                    toggle: function () {
                        this.state.opened = !this.state.opened
                    },
                    selectionDisabled: function () {
                        return (!!self.bookingDateConfig[0].model) ? false : true;
                    }
                }
            ];

            function bookingValidEndDate(){
                return true;
            }

        }

    }

})(window.angular);