'use strict';

(function () {

    angular
        .module('nf.dataservices')
        .factory('dataServiceUtil', dataServiceUtil);

    dataServiceUtil.$inject = [
        '$timeout',
        '$http',
        '$log',
        '$q',
        'CONF'
    ];

    function dataServiceUtil($timeout, $http, $log, $q, CONF) {
        var  _map = {};
        // interfaccia
        var factoryService = {
        	callNFService: callNFService
        };

        try{
            init();
        } catch(e){
            $log.error(e);
            $log.info("***  si è verificato un errore durante la registrazione dei servizi ***");
        }

        return factoryService;


        function init(){
            _.each(CONF.services,function(service){

                // default value
                var confSrv = $.extend({
                    "name": "",
                    "method": "GET",
                    "url": "",
                    "mocked": {
                        isMocked: false,
                        file: "",
                        timeout: 0
                    }
                },service);

                _map[service.name] = confSrv;

            });
        }

        //funzione per le chiamate a servizio in GET
        function callNFService(name, data) {

            var conf;
            var url;
            var method;
            var timeout;
            var _data = data || {};
            var servicesMocked;
            try {
                conf =  _map[name];
                if(!conf)
                    throw "Non esiste nessun servizio registrato:" + name;
                servicesMocked = isServicesMocked(conf);
                // controllo se il servizio è mockato.
                if(servicesMocked){
                    url = conf.mocked.file;
                    method = "GET";
                    timeout = conf.mocked.timeout;

                }
                else {
                    url = CONF.baseUrl +  conf.url;
                    method = conf.method;
                    timeout = conf.timeout;
                }
                return  getPromise(url, method);
	        } catch (e) {
                $log.error(e);
                return $q.reject(e);
            }

            // Funzione interna per accedere ai dati della funzione.
            function getPromise(){
                var retValue;
                var deferred;

                if(timeout){
                    deferred = $q.defer();
                    retValue = deferred.promise;
                    $timeout(function(){
                        callHttp().then(deferred.resolve,deferred.reject);
                    }, timeout);
                }
                else{
                    retValue = callHttp();
                }

                return retValue;

                /*
                * Effettua l'effettiva chiamata
                */
                function callHttp(){
                    var opt = {
                        url: url,
                        method: method,
                        headers: {
                            'Cache-Control': 'no-cache',
                            'Content-Type': 'application/json;charset=UTF-8'
                        }
                    };

                    switch(conf.method){
                        case "GET" :
                        	if(!servicesMocked){
                        		opt.params = _data;
                        	}
                            break;
                        case "DELETE" :
                        	opt.params = _data;
                            break;
                        default:
                            opt.data = _data;
                            break;
                    }

                    // restituisce la promessa
                    return $http(opt).then(handleNotManagedError);
                }
            }
        }

        function isServicesMocked(localConf) {

            if (!angular.isUndefined(CONF.allServicesMocked.forced) && CONF.allServicesMocked.forced != null ) {
                return CONF.allServicesMocked.forced;
            } else if (!angular.isUndefined(localConf.mocked)
                && localConf.mocked !== "") {
                //se è valorizzato
                return localConf.mocked.isMocked;
            } else {
                //default
                return CONF.allServicesMocked.def;
            }
        }

        //GESTIONE DEL CASO DI ERRORE
        function handleNotManagedError(response) {

            if($.isPlainObject(response.data) && "errore" in response.data) {

                return $q.reject(response.data);

            } else {

                return response;
            }

        }

    }
})();
