'use strict';

(function () {

  angular
    .module('nf.home')
    .controller('homeController', homeController);

  homeController.$inject = ['$scope',
                            '$state',
                            'productsList',
                            'modalSrv'
  ];


  function homeController($scope,
                          $state,
                          productsList,
                          modalSrv) {
    var self = this;

    self.openBookProductForm = openBookProductForm;
    self.openProductDetails = openProductDetails;
    self.selectPriceOption = selectPriceOption;

    self.listaFurgone = [];
    self.selectedProduct = {};

    init();

    function init() {
      self.furgoneList = productsList.furgoneList;
      self.carsList = productsList.carsList;
    }

    function openBookProductForm(product) {
      self.selectedProduct = angular.copy(product);
      var productForm = {
        innerTemplateUrl: "views/home/modals/bookModal.tpl.html",
        title: "Prenota",
        scope: $scope,
        callbackOk: bookProduct,
        nameOk: "Prenota",
        callbackCancel: angular.noop,
        nameAnnulla: "Annulla"
      };
      modalSrv.showModal(productForm);
    }

    function selectPriceOption(index){
        angular.forEach(self.selectedProduct.price_options,function(value, key) {
          if(key !== index) value.selected = false;
          else value.selected = true;
        });
    }

    function bookProduct(){
      var selectedOption;
       angular.forEach(self.selectedProduct.price_options, function (value, key) {
         if (value.selected) selectedOption = value.id;
       });
      $state.go('booking', {'prod': self.selectedProduct.id, 'price': selectedOption});
    }
    
    function openProductDetails(product) {
      self.selectedProduct = product;
      var productDetail = {
        innerTemplateUrl: "views/home/modals/productDetail.tpl.html",
        title: "Dettaglio",
        scope: $scope,
        callbackOk: angular.noop,
        nameOk: "Chiudi"
      };
      modalSrv.showModal(productDetail);
    }

  }
})();
