'use strict';
(function () {

    angular
        .module('directives.modal')
        .controller('modalController', modalController);


    modalController.$inject = [
        "$log",
        "$scope",
        "$uibModalInstance",
        "conf"
    ];

    function modalController($log, $scope, $uibModalInstance, conf) {
        var self = this;

        self.conf = $.extend({}, conf);

        self.ok = ok;
        self.cancel = cancel;

        init();

        function init() {
            self.showCancel = self.conf.callbackCancel !== $.noop;
            self.nameCancel = self.conf.nameCancel || "Annulla";
            self.showOk = self.conf.callbackOk !== $.noop;
            self.nameOk = self.conf.nameOk || "OK";
            self.title = self.conf.title || "";
        }

        function ok() {
            $uibModalInstance.close();
        }

        function cancel() {
            $uibModalInstance.dismiss();
        }
    }
})();