(function (angular) {

    angular.module('directives.contacts').directive("nfContacts", contactsFn);

    function contactsFn() {
        var directive = {
            restrict: 'E',
            templateUrl: 'directives/contacts/contacts.template.html',
            scope: {

            },
            replace: true,
            controller: contactFn,
            controllerAs: 'c'
        };

        return directive;

        function contactFn() {

            var self = this;

            self.address = "Via Gasperina 115 cap 00118 Roma";
            self.openingTime = {
                ordinary: {
                    label: "Lun-Ven",
                    value: "8:30-13:00 15:00-19:00"
                },
                extra: {
                    label: "Sab",
                    value: "8:30-12:00 15:00-19:00"
                }
            };
            self.tels = ["1234567890"];

        }

    }

})(window.angular);