'use strict';

(function () {

    angular
        .module('nf.dataservices')
        .factory('productSrv', productSrv);

    productSrv.$inject = [
        '$log',
        'dataServiceUtil'
    ];

    function productSrv($log, dataServiceUtil) {

        return {
            getProductDetail: getProductDetail
        };

        function getProductDetail(param) {
            try {
                return dataServiceUtil.callNFService("getProductDetail", param).then(handleSuccess);
            } catch (e) {
                $log.error("*** si e' verificato un errore nel servizio get getProductDetail ***");
            }

        }

        function handleSuccess(response) {
            return response;
        }

    }

})();
