'use strict';

(function () {

    angular
        .module('nf.dataservices')
        .factory('carsSrv', carsSrv);

    carsSrv.$inject = [
        '$log',
        'dataServiceUtil'
    ];

    function carsSrv($log, dataServiceUtil) {

        return {
            getCars: getCars
        };

        function getCars(param) {
            try {
                return dataServiceUtil.callNFService("getCars", param).then(handleSuccess);
            } catch (e) {
                $log.error("*** si e' verificato un errore nel servizio get getCars ***");
            }

        }

        function handleSuccess(response) {
            return response;
        }

    }

})();
