'use strict';

(function () {

  angular
    .module('nf.booking')
    .controller('bookingController', bookingController);

  bookingController.$inject = ["$log",
                               "$scope",
                               "$stateParams",
                               "modalSrv",
                               "bookingResolve",
                               "alertSrv"
  ];

  function bookingController($log,
                             $scope,
                             $stateParams,
                             modalSrv,
                             bookingResolve,
                             alertSrv) {
    var self = this;

    self.product = {};
    self.priceOption = {};
    
    self.selectAccessory = selectAccessory;
    self.openEditBookingData = openEditBookingData;
    self.openProductDetails = openProductDetails;
    self.bookProduct = bookProduct;

    init();

    function init() {
      self.product = bookingResolve.productDetail;
      self.selectedProduct = angular.copy(self.product);
      self.priceOption = _.find(self.product.price_options, {id: self.product.selected_price_option});
    }

    function selectAccessory(index) {
      angular.forEach(self.selectedProduct.accessories, function (value, key) {
        if (key === index){
          value.selected = !value.selected;
        }
        //else value.selected = true;
      });
    }

    function openEditBookingData() {
      var productForm = {
        innerTemplateUrl: "views/home/modals/bookModal.tpl.html",
        title: "Modifica",
        scope: $scope,
        callbackOk: editBookingData,
        nameOk: "Modifica",
        callbackCancel: angular.noop,
        nameAnnulla: "Annulla"
      };
      modalSrv.showModal(productForm);
    }

    function editBookingData() {
      
    }

    function openProductDetails() {
      var productDetail = {
        //size: "lg",
        innerTemplateUrl: "views/home/modals/productDetail.tpl.html",
        title: "Dettaglio",
        scope: $scope,
        callbackOk: angular.noop,
        nameOk: "Chiudi"
      };
      modalSrv.showModal(productDetail);
    }

    function bookProduct() {
      console.log(self.selectedProduct.accessories);
      var errObj = {
        type: "success",
        msg: ["prova messagio alert"],
        size: "large",
        position: "top"
      };
      alertSrv.showAlert(errObj);
    }

  }
})();