'use strict';

(function(){
    angular.module('directives.alert')
        .directive('blAlertBtn',blAlertBtn);

    blAlertBtn.$inject = ["$log","$compile","$document"];

    function blAlertBtn($log,$compile,$document){

        var directive = {
            controller: alertBtnController,
            controllerAs: "c",
            link: function(scope,element,attrs){
                var template = '<bl-alert text="'+attrs.alertText+'"></bl-alert>';
                element.on("click", function() {
                    scope.$apply(function() {
                        var content = $compile(template)(scope);
                        $document.find('body').append(content);
                    })
                });
            }
        };

        return directive;
    }

    alertBtnController.$inject = ['$log'];

    function alertBtnController($log){
        var self = this;
    }

})();