(function (angular) {

angular.module('directives.header').directive("nfHeader", headerFn);

function headerFn($state) {
    var directive = {
        restrict: 'E',
        templateUrl: 'directives/header/header.template.html',
        scope: {

        },
        controller: headerFn,
        controllerAs: 'c'
    };

    return directive;

    function headerFn($state) {
        
        var self = this;

        self.mainMenu = [{label: "Home", reference: "home"},
                         {label: "Contatti",reference: "contact"},
                         {label: "Chi siamo",reference: "about"},
                         {label: "termini e condizioni",reference: "policies"}];


    }

}

})(window.angular);