'use strict';

(function () {

    angular.module("nf.about")
        .config(config);

    // dependecy injection
    config.$inject = ["$stateProvider", "$urlRouterProvider"];

    function config($stateProvider, $urlRouterProvider) {

        var state = {
            name: 'about',
            url: '/about',
            templateUrl: 'views/about/about.html',
            controller: 'aboutController',
            controllerAs: "c",
            resolve: {
                aboutResolve: aboutResolve
            }
        };

        $stateProvider.state(state);

        aboutResolve.$inject = ['$stateParams'];

        function aboutResolve($stateParams) {
            return 'about page';
        }
    }
})();
