'use strict';

(function () {

    angular.module("nf.booking")
        .config(config);

    // dependecy injection
    config.$inject = ["$stateProvider", "$urlRouterProvider"];

    function config($stateProvider, $urlRouterProvider) {

        var state = {
            name: 'booking',
            url: '/booking/:prod/:price',
            templateUrl: 'views/booking/booking.html',
            controller: 'bookingController',
            controllerAs: "c",
            resolve: {
                bookingResolve: bookingResolve
            }
        };

        $stateProvider.state(state);

        bookingResolve.$inject = ['$stateParams', '$q', 'productSrv'];

        function bookingResolve($stateParams, $q, productSrv) {
            var prodId = $stateParams.prod;
            var priceOptionId = $stateParams.price;
            var response = {};

            var productDetailCall = productSrv.getProductDetail({prodId: prodId, priceOptionId: priceOptionId}).then(function (data) {
                return data.data.product;;
            }).catch(function (response) {
                return response;
            });

            //TODO spinner on
            return $q.all([productDetailCall]).then(function (data) {
                //TODO spinner off
                var failedServices = 0,
                    errors = [];
                angular.forEach(data, function (value, key) {
                    //TODO manage errors
                    if (typeof value.status !== 'undefined' && value.status != 200) {
                        errors.push(value);
                        failedServices += 1;
                    }
                });
                if (failedServices > 0) {
                    //TODO show errors
                    return $q.reject();
                } else {
                    response.productDetail = data[0];
                    return response;
                }
            });
        }
    }
})();
