'use strict';



(function(){
    angular.module('directives.alert')
        .component('blAlert',{
            restrict: 'E',
            templateUrl: 'directives/alert/alert.html',
            bindings : {
                config : "@?"
            },
            controller: alertController,
            controllerAs: 'c'
        });


    alertController.$inject = ['$log','$element'];

    function alertController($log, $element){
        var self = this;

        self.$onInit = init;
        self.closeAlert = closeAlert;

        function init(){
            self.config = atob(self.config);
            self.conf = JSON.parse(self.config);
            self.conf.msg = self.conf.msg;
            self.conf.position = self.conf.size === "lg" ? "top" :  self.conf.position;
        }

        function closeAlert(){
            $element.remove();
        }

    }





})();