'use strict';

(function () {


    var TEMPLATE_URL = "directives/modal/modal.html";

    angular
        .module('directives.modal')
        .factory('modalSrv', modal);

    modal.$inject = [
        '$log',
        '$uibModal'
    ];

    function modal($log, $uibModal) {

        return {
            showModal: showModal
        };

        function showModal(data) {
            var _callbackOk = $.isFunction(data.callbackOk) ? data.callbackOk : $.noop;
            var _callbackCancel = $.isFunction(data.callbackCancel) ? data.callbackCancel : $.noop;
            var _callbackX = $.isFunction(data.callbackX) ? data.callbackX : $.noop;
            var backdrop = data.backdrop || true;
            var conf = {
                "innerTemplateUrl": data.innerTemplateUrl || null,
                'htmlText': data.htmlText || null,
                "title": data.title || null,
                "callbackOk": _callbackOk,
                "callbackCancel": _callbackCancel,
                "callbackX": _callbackX,
                "nameOk": data.nameOk || null,
                "nameCancel": data.nameCancel || null
            };

            $uibModal.open({
                animation: false,
                backdrop: backdrop,
                scope: data.scope || null,
                templateUrl: TEMPLATE_URL,
                controller: "modalController",
                controllerAs: "modalCtrl",
                size: data.size || "",
                windowClass: "animated rollIn",
                resolve: {
                    conf: conf
                }
            }).result.then(_callbackOk, reject);

            function reject() {
                _callbackAnnulla();
                _callbackX();
            }

        }

    }

})();