'use strict';

(function () {

    angular.module("nf.contact")
        .config(config);

    // dependecy injection
    config.$inject = ["$stateProvider", "$urlRouterProvider"];

    function config($stateProvider, $urlRouterProvider) {

        var state = {
            name: 'contact',
            url: '/contact',
            templateUrl: 'views/contact/contact.html',
            controller: 'contactController',
            controllerAs: "c",
            resolve: {
                contactResolve: contactResolve
            }
        };

        $stateProvider.state(state);

        contactResolve.$inject = ['$stateParams'];

        function contactResolve($stateParams) {
            return 'contact page';
        }
    }
})();
