'use strict';

(function () {

    angular
        .module('nf.dataservices')
        .factory('furgoneSrv', furgoneSrv);

    furgoneSrv.$inject = [
        '$log',
        'dataServiceUtil'
    ];

    function furgoneSrv($log, dataServiceUtil) {

        return {
        	getFurgone: getFurgone
        };

        function getFurgone(param) {
            try {
            	return dataServiceUtil.callNFService("getFurgone", param).then(handleSuccess);
            }
            catch (e) {
                $log.error("*** si e' verificato un errore nel servizio get getFurgone ***");
        	}

        }

        function handleSuccess(response){
        	return response;
        }

    }

})();
