'use strict';

(function () {

    angular.module("nf.policies")
        .config(config);

    // dependecy injection
    config.$inject = ["$stateProvider", "$urlRouterProvider"];

    function config($stateProvider, $urlRouterProvider) {

        var state = {
            name: 'policies',
            url: '/policies',
            templateUrl: 'views/policies/policies.html',
            controller: 'policiesController',
            controllerAs: "c",
            resolve: {
                policiesResolve: policiesResolve
            }
        };

        $stateProvider.state(state);

        policiesResolve.$inject = ['$stateParams'];

        function policiesResolve($stateParams) {
            return 'policies page';
        }
    }
})();
